@import url("https://fonts.googleapis.com/css2?family=Cinzel+Decorative:wght@400;700;900&family=Montserrat:ital,wght@0,100..900;1,100..900&family=Cinzel:wght@400..900&display=swap");
* {
  font-family: "Montserrat", "Cinzel Decorative";
}
* :focus-visible {
  outline: none;
}

::-webkit-scrollbar {
  width: 0px;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
a {
  margin: 0px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "cinzel";
}

p {
  font-family: "montserrat";
}

a {
  text-decoration: none;
  color: #000000;
}

.top-menu {
  width: 100dvw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  height: 100px;
}

.top-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: calc(100dvw - 86px);
  height: 43px;
}
.top-bar h1 {
  color: #F2F2F2;
  font-weight: 400;
}
.top-bar .menu-btn {
  height: 22.2px;
  width: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.top-bar .menu-btn .line {
  width: 100%;
  height: 10%;
  background-color: #F2F2F2;
}

.side-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100dvw;
  height: 100dvh;
  background-color: #171717;
  position: fixed;
  top: 0px;
  right: -110%;
  transition: right 0.6s ease-out;
  z-index: 1;
}
.side-bar .container {
  width: calc(100% - 120px);
  height: 90%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.side-bar .container .left,
.side-bar .container .right {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;
  height: 100%;
}

.nav-main-link {
  display: flex;
  align-items: center;
  gap: 15px;
}
.nav-main-link h1 {
  color: rgba(255, 255, 255, 0.2);
  font-weight: 100;
}
.nav-main-link P {
  color: #FFFFFF;
}
.nav-main-link a {
  display: flex;
  align-items: center;
  gap: 10px;
}

.category {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.category h1 {
  color: #F2F2F2;
  font-weight: 100;
}
.category .category-header {
  font-size: 2.6rem;
}
.category .sub-category {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.category .sub-category > h1 {
  font-size: 1.8rem;
}
.category .sub-category .links {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.category .sub-category .links a {
  display: flex;
  align-items: center;
  gap: 10px;
  color: #F2F2F2;
}

.contact {
  position: relative;
  width: 100dvw;
  height: 100dvh;
  background-color: #F2F2F2;
}
.contact label {
  display: flex;
  align-items: center;
  position: relative;
  width: 100%;
  height: 120px;
  border-top: solid 1.5px #000000;
}
.contact label > h1 {
  margin-left: 60px;
}
.contact label span {
  width: 20%;
  margin-left: 60px;
}
.contact label span h1 {
  font-weight: 100;
}
.contact label input {
  display: flex;
  align-items: center;
  height: 100%;
  width: 100%;
  background-color: #F2F2F2;
  border: none;
  padding: 0px;
}
.contact label input :focus-visible {
  outline: none;
}
.contact .submit-button {
  display: flex;
  align-items: center;
  position: absolute;
  bottom: 0px;
  height: 120px;
  width: 100%;
  background-color: #000000;
  color: #F2F2F2;
}
.contact .submit-button h1 {
  margin-left: 60px;
}

.footer {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 120px;
  width: 100dvw;
  background-color: #F2F2F2;
}
.footer .left,
.footer .right {
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer .right {
  gap: 15px;
}

.page {
  width: 100dvw;
  height: 100dvh;
  overflow-x: hidden;
  overflow-y: auto;
}

.landing-banner {
  display: flex;
  flex-direction: column;
  width: 100dvw;
  height: 100dvh;
  background-image: url(./images/products/landingbannerimage.jpg);
  background-size: cover;
  background-position: bottom;
  filter: grayscale(1);
  overflow-x: hidden;
}
.landing-banner .filter {
  height: 100%;
  width: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0) 100%);
}
.landing-banner .top-menu {
  position: absolute;
}

.brand-info-section {
  height: 1080px;
  width: 100dvw;
  position: relative;
}
.brand-info-section .product-1 {
  height: 58%;
  width: 38%;
  object-fit: cover;
  position: relative;
  top: 50px;
  filter: brightness(40%);
}
.brand-info-section .product-2 {
  height: 39%;
  width: 51%;
  object-fit: cover;
  position: absolute;
  bottom: 50px;
  right: 0px;
  filter: grayscale(1);
}
.brand-info-section .texts {
  display: flex;
  flex-direction: column;
  gap: 22px;
  width: 404px;
  height: 346px;
  position: absolute;
  top: 20%;
  left: 45%;
}

.landing-page-product-1,
.landing-page-product-3 {
  height: 80vh;
  width: 100dvw;
  display: flex;
  align-items: center;
  gap: 50px;
}
.landing-page-product-1 .left,
.landing-page-product-3 .left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  width: 70%;
  color: #F2F2F2;
  margin: 0px 0px 0px 40px;
  gap: 10px;
}
.landing-page-product-1 .left p,
.landing-page-product-3 .left p {
  text-align: end;
}
.landing-page-product-1 .right,
.landing-page-product-3 .right {
  position: relative;
  right: 0px;
  width: 100%;
  height: calc(100% - 100px);
}
.landing-page-product-1 .right img,
.landing-page-product-3 .right img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  filter: brightness(60%);
}

.landing-page-product-1 {
  background-color: #171717;
}

.landing-page-product-2 {
  display: flex;
  align-items: center;
  height: 100dvh;
  position: relative;
}
.landing-page-product-2 .filter {
  width: 100%;
  height: 100%;
  background-image: url(./images/products/product1-2.jpg);
  background-size: cover;
  background-position: center;
  filter: brightness(50%);
}
.landing-page-product-2 .text {
  display: flex;
  flex-direction: column;
  width: 404px;
  position: absolute;
  left: 70px;
  color: #F2F2F2;
  gap: 22px;
}

.landing-page-product-3 .left {
  color: #171717;
}

.ordinary-challenge {
  position: relative;
  height: 1000px;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.ordinary-challenge .left,
.ordinary-challenge .right {
  height: 100%;
  width: 100%;
}
.ordinary-challenge .left {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 50px;
  padding: 0px 0px 60px 0px;
  height: calc(100% - 60px);
}
.ordinary-challenge .left .image {
  height: 360px;
  width: 80%;
}
.ordinary-challenge .left .image img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  filter: grayscale(0.95);
}
.ordinary-challenge .left .texts {
  display: flex;
  flex-direction: column;
  gap: 22px;
  position: relative;
  left: 40px;
  width: 62%;
  align-items: flex-end;
  text-align: end;
}
.ordinary-challenge .right {
  background-image: url(./images/products/product5-1.jpg);
  background-size: cover;
  background-position: center;
}

.top-menu {
  position: absolute;
  z-index: 1;
}

.product {
  height: 100dvh;
  width: 100dvw;
  position: sticky;
  top: 0px;
}
.product img {
  position: relative;
  top: 0px;
  height: 100%;
  width: 100%;
  object-fit: cover;
  filter: brightness(50%);
}
.product .descrip {
  display: grid;
  align-items: center;
  gap: 10px;
  grid-template-columns: 1fr 4fr;
  width: 500px;
  position: absolute;
  bottom: 60px;
  left: 60px;
  color: #F2F2F2;
}
.product .descrip h1 {
  grid-column: span 2;
  font-weight: 100;
}

.page.about-page {
  background-color: #000000;
}
.page.about-page .top-menu {
  background-color: #F2F2F2;
  position: relative;
}
.page.about-page .top-menu .top-bar h1 {
  color: #000000;
}
.page.about-page .top-menu .menu-btn > .line {
  background-color: #000000;
}
.page.about-page .top-bar {
  z-index: 1;
}
.page.about-page .overview-div {
  height: calc(100dvh - 100px);
  width: 100dvw;
  position: relative;
  background-color: #F2F2F2;
  top: -5%;
}
.page.about-page .overview-div .image-1 {
  height: 105%;
  width: 53vh;
  position: absolute;
  top: 10%;
  left: 0px;
  background-size: 278%;
  background-position-x: 30%;
  background-position-y: 90%;
  filter: brightness(50%);
}
.page.about-page .overview-div .image-2 {
  height: 45vh;
  width: 47%;
  position: absolute;
  bottom: -34%;
  right: 0px;
  background-size: 260%;
  background-position-y: 490px;
  background-position-x: 47%;
  filter: grayscale(100%) brightness(80%);
}
.page.about-page .overview-div .texts {
  display: flex;
  flex-direction: column;
  gap: 10px;
  max-width: 430px;
  height: 65%;
  position: absolute;
  top: 13%;
  left: calc(53vh + 60px);
  overflow-y: hidden;
}
.page.about-page .services {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100dvw;
}
.page.about-page .services .main-image {
  margin: 35vh 0px 8vh 0px;
  width: 80%;
  height: 80dvh;
  background-size: 100%;
  background-position-y: 65%;
  filter: brightness(60%);
}
.page.about-page .services .texts {
  display: flex;
  width: 85%;
  gap: 30px;
  color: #F2F2F2;
  margin: 0px 0px 50px 0px;
}
.page.about-page .services .texts .left {
  width: calc(30% - 30px);
}
.page.about-page .services .texts .left h1 {
  font-weight: 100;
}
.page.about-page .services .texts .right {
  width: 70%;
  display: grid;
  gap: 30px;
  grid-template-columns: 1fr 1fr;
}
.page.about-page .services .texts .right .descrip {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

