// Import
@import url('https://fonts.googleapis.com/css2?family=Cinzel+Decorative:wght@400;700;900&family=Montserrat:ital,wght@0,100..900;1,100..900&family=Cinzel:wght@400..900&display=swap');

// Variables
$primary-color: #000000;
$primary-color2: #F2F2F2;
$top-menu-height: 43px;

@mixin fdja {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

;

@mixin fdj {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

;

@mixin fda {
    display: flex;
    flex-direction: column;
    align-items: center
}

;

@mixin fja {
    display: flex;
    justify-content: center;
    align-items: center;
}

;

@mixin fj {
    display: flex;
    justify-content: center;
}

;

@mixin fa {
    display: flex;
    align-items: center
}

;

@mixin fd {
    display: flex;
    flex-direction: column;
}

// General
* {
    font-family: 'Montserrat', "Cinzel Decorative";
    & :focus-visible{
        outline: none;
    }
}

::-webkit-scrollbar {
    width: 0px;
}

// .App {
//     text-align: center;
//     color: $primary-color;
//     background-color: $primary-color2;
// }
h1,
h2,
h3,
h4,
h5,
h6,
p,
a {
    margin: 0px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: "cinzel";
}

p {
    font-family: "montserrat";
}

a {
    text-decoration: none;
    color: $primary-color;
}

// Components
// Components - Top Bar
.top-menu{
    width: 100dvw;
    @include fdja;
    overflow: hidden;
    height: 100px;
}
.top-bar {
    @include fa;
    justify-content: space-between;
    width: calc(100dvw - (2 * $top-menu-height));
    height: $top-menu-height;
    h1 {
        color: $primary-color2;
        font-weight: 400;
    }
    .menu-btn {
        height: 22.2px;
        width: 40px;
        @include fda;
        justify-content: space-between;
        .line {
            width: 100%;
            height: 10%;
            background-color: $primary-color2;
        }
    }
}
// Components - Side Bar
.side-bar{
    @include fja;
    width: 100dvw;
    height: 100dvh;
    background-color: #171717;
    position: fixed;
    top: 0px;
    right: -110%;
    transition: right 0.6s ease-out;
    z-index: 1;
    .container{
        width: calc(100% - 120px);
        height: 90%;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        // h1{
        //     color: $primary-color2;
        // }
        .left,
        .right{
            @include fd;
            gap: 20px;
            width: 100%;
            height: 100%;
        }
    }
}
.nav-main-link{
    @include fa;
    gap: 15px;
    h1{
        color: rgb(255, 255, 255, .2);
        font-weight: 100;
    }
    P{
        color: #FFFFFF
    }
    a{
        @include fa;
        gap: 10px;
    }
}
.category{
    @include fd;
    gap: 20px;
    h1{
        color: $primary-color2;
        font-weight: 100;
    }
    .category-header{
        font-size: 2.6rem;
    }
    .sub-category{
        @include fd;
        gap: 10px;
        & >h1{
            font-size: 1.8rem
        }
        .links{
            @include fd;
            gap: 10px;
            a{
                @include fa;
                gap: 10px;
                color: $primary-color2
            }
        }
    }
}

// Components - Contact
.contact{
    position: relative;
    width: 100dvw;
    height: 100dvh;
    background-color: $primary-color2;
    label{
        @include fa;
        position: relative;
        width: 100%;
        height: 120px;
        border-top: solid 1.5px $primary-color;
        & > h1{
            margin-left: 60px;
        }
        span{
            width: 20%;
            margin-left: 60px;
            h1{
                font-weight: 100;
            }
        }
        input{
            @include fa;
            height: 100%;
            width: 100%;
            background-color: $primary-color2;
            border: none;
            padding: 0px;
            & :focus-visible{
                outline: none;
            }
        }
    }
    .submit-button{
        @include fa;
        position: absolute;
        bottom: 0px;
        height: 120px;
        width: 100%;
        background-color: $primary-color;
        color: $primary-color2;
        h1{
            margin-left: 60px;
        }
    }
}

// Components - Footer
.footer{
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 120px;
    width: 100dvw;
    background-color: $primary-color2;
    .left,
    .right{
        @include fja;
    }
    // .left{
    //     justify-content: center;
    // }
    .right{
        gap: 15px;
    }
}

// Pages
.page{
    width: 100dvw;
    height: 100dvh;
    overflow-x: hidden;
    overflow-y: auto;
}
// Pages - Landing Page
.landing-banner {
    @include fd;
    width: 100dvw;
    height: 100dvh;
    background-image: url(./images/products/landingbannerimage.jpg);
    background-size: cover;
    background-position: bottom;
    filter: grayscale(1);
    overflow-x: hidden;
    .filter {
        height: 100%;
        width: 100%;
        background: linear-gradient(rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0) 100%);
    }
    .top-menu{
        position: absolute;
    }
}

.brand-info-section {
    // height: 130vh;
    height: 1080px;
    width: 100dvw;
    position: relative;

    .product-1 {
        height: 58%;
        width: 38%;
        object-fit: cover;
        position: relative;
        top: 50px;
        filter: brightness(40%);
    }

    .product-2 {
        height: 39%;
        width: 51%;
        object-fit: cover;
        position: absolute;
        bottom: 50px;
        right: 0px;
        filter: grayscale(1);
    }

    .texts {
        @include fd;
        gap: 22px;
        width: 404px;
        height: 346px;
        position: absolute;
        top: 20%;
        left: 45%;
    }
}

.landing-page-product-1,
.landing-page-product-3 {
    height: 80vh;
    width: 100dvw;
    @include fa;
    gap: 50px;

    .left {
        @include fdj;
        align-items: flex-end;
        width: 70%;
        color: #F2F2F2;
        margin: 0px 0px 0px 40px;
        gap: 10px;
        p {
            text-align: end;
        }
    }

    .right {
        position: relative;
        right: 0px;
        width: 100%;
        height: calc(100% - 100px);

        img {
            height: 100%;
            width: 100%;
            object-fit: cover;
            filter: brightness(60%);
        }
    }
}

.landing-page-product-1 {
    background-color: #171717;
}

.landing-page-product-2 {
    @include fa;
    height: 100dvh;
    position: relative;
    .filter {
        width: 100%;
        height: 100%;
        background-image: url(./images/products/product1-2.jpg);
        background-size: cover;
        background-position: center;
        filter: brightness(50%);
    }

    .text {
        @include fd;
        width: 404px;
        position: absolute;
        left: 70px;
        color: #F2F2F2;
        gap: 22px;
    }
}

.landing-page-product-3 {
    .left {
        color: #171717;
    }
}

.ordinary-challenge {
    position: relative;
    height: 1000px;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);

    .left,
    .right {
        height: 100%;
        width: 100%;
    }

    .left {
        @include fd;
        justify-content: flex-end;
        gap: 50px;
        padding: 0px 0px 60px 0px;
        height: calc(100% - 60px);

        .image {
            height: 360px;
            width: 80%;

            img {
                height: 100%;
                width: 100%;
                object-fit: cover;
                filter: grayscale(.95);
            }
        }

        .texts {
            @include fd;
            gap: 22px;
            position: relative;
            left: 40px;
            width: 62%;
            align-items: flex-end;
            text-align: end;
        }
    }

    .right {
        background-image: url(./images/products/product5-1.jpg);
        background-size: cover;
        background-position: center;
    }
}

// Pages - Products
.top-menu{
    position: absolute;
    z-index: 1;
}
.product{
    height: 100dvh;
    width: 100dvw;
    position: sticky;
    top: 0px;
    img{
        position: relative;
        top: 0px;
        height: 100%;
        width: 100%;
        object-fit: cover;
        filter: brightness(50%);
    }
    .descrip{
        display: grid;
        align-items: center;
        gap: 10px;
        grid-template-columns: 1fr 4fr;
        width: 500px;
        position: absolute;
        bottom: 60px;
        left: 60px;
        color: $primary-color2;
        h1{
            grid-column: span 2;
            font-weight: 100;
        }
    }
}

// Pages - About Page
.page.about-page{
    background-color: $primary-color;
    .top-menu{
        background-color: $primary-color2;
        position: relative;
        .top-bar h1{
            color: $primary-color;
        }
        .menu-btn > .line{
            background-color: $primary-color;
        }
    }
    .top-bar{
        z-index: 1;
        // color: $primary-color;
        // .menu-btn .line{
        //     background-color: $primary-color;
        // }
    }
    .overview-div{
        height: calc(100dvh - 100px);  // 100px is the top menus hight
        width: 100dvw;
        position: relative;
        background-color: $primary-color2;
        top: -5%;
        .image-1{
            height: 105%;
            width: 53vh;
            position: absolute;
            top: 10%;
            left: 0px;
            background-size: 278%;
            background-position-x: 30%;
            background-position-y: 90%;
            filter: brightness(50%);
        }
        .image-2{
            height: 45vh;
            width: 47%;
            position: absolute;
            bottom: -34%;
            right: 0px;
            background-size: 260%;
            background-position-y: 490px;
            background-position-x: 47%;
            filter: grayscale(100%) brightness(80%);
        }
        .texts{
            @include fd;
            gap: 10px;
            max-width: 430px;
            height: 65%;
            position: absolute;
            top: 13%;
            left: calc(53vh + 60px);
            overflow-y: hidden
        }
    }
    .services{
        @include fda;
        width: 100dvw;
        .main-image{
            margin: 35vh 0px 8vh 0px;
            width: 80%;
            height: 80dvh;
            background-size: 100%;
            background-position-y: 65%;
            filter: brightness(60%);
        }
        .texts{
            display: flex;
            width: 85%;
            gap: 30px;
            color: $primary-color2;
            margin: 0px 0px 50px 0px;
            .left{
                width: calc(30% - 30px);
                h1{
                    font-weight: 100;
                }
            }
            .right{
                width: 70%;
                display: grid;
                gap: 30px;
                grid-template-columns: 1fr 1fr;
                .descrip{
                    @include fd;
                    gap: 10px;
                }
            }
        }
    }
}